import { 
  getArticles, 
  getYourFeedArticles,
  addFavorite,
  deleteFavorite
  } from '@/api/article'
import { getTags } from '@/api/tag'
import { mapState } from 'vuex'
export default {
  name: 'HomePage',
  async asyncData ({ query }) {
    const page = parseInt(query.page || 1)
    const limit = 40
    const tab = query.tab || 'global_feed'
    const tag = query.tag

    const loadArticles = tab === 'your_feed' ? getYourFeedArticles : getArticles

    const [articleRes, tagRes] = await Promise.all([
      loadArticles({
        limit,
        offset: (page - 1) * limit,
        tag: tag 
      }),
      getTags()
    ])
    const {articles, articlesCount} = articleRes.data
    const { tags } = tagRes.data

    articles.forEach(article => article.favoriteDisabled = false)
    return {
      articles: articles, // 文章列表
      articlesCount: articlesCount, // 文章总数
      page, // 页码
      limit, // 页容量
      // 数据太多 ，随便取前29个
      tags: tags.splice(0, 20), // 标签列表
      tab, // tab 栏
      tag, // 当前标签
    }
  },
  watchQuery: ['page', 'tag', 'tab'],
  computed: {
    ...mapState(['user']),
    totalPage(){
      return Math.ceil( this.articlesCount / this.limit )
    }
  },
  methods: {
    async onFavorite(art){
      art.favoriteDisabled = true
      if(art.favorited){
        await deleteFavorite(art.slug)
        art.favorited = false
        art.favoritesCount += -1
      }else{
        await addFavorite(art.slug)
        art.favorited = true
        art.favoritesCount += 1
      }
      art.favoriteDisabled = false
    }
  }
};
